





#projects {
    width: 100%;
    background-image: url('./assets/galaxy.jpg') !important; 
    padding-bottom: 20%;
}

#projects>.carousel>.project-square {
    display: flex;
    flex-direction: column;
    width: 40%;
    justify-content: center;
    align-items: center;
    margin: auto;
}

#projects>.carousel>.project-square>img {
    width: 100%;
    height: 200%;
    border-radius: 20px;
    border: solid white 1px;

}

#projects>.carousel>.project-square>img:hover {
cursor: pointer;
}
